.products {
  padding-top: 66px;
  padding-bottom: 140px;

  background-color: $color-black-pearl;

  @include vp-1023 {
    padding-top: 54px;
    padding-bottom: 45px;
  }

  @include vp-767 {
    padding-top: 46px;
    padding-bottom: 56px;
  }
}

.products__title {
  margin: 0 0 54px;
  padding: 0;

  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  font-family: $ff-roboto-condensed;
  color: $color-white;

  @include vp-1023 {
    margin: 0 0 38px;
  }

  @include vp-767 {
    margin: 0 20px 30px 0;
  }

  &--desktop {
    @include vp-767 {
      display: none;
    }
  }

  &--mobile {
    display: none;

    @include vp-767 {
      display: block;

      font-size: 28px;
      line-height: 39px;
    }
  }
}

.products__list {
  display: grid;
  margin: 0;
  padding: 0;

  list-style-type: none;

  gap: 85px;
  grid-template-columns: repeat(3, 1fr);

  @include vp-1023 {
    display: block;
  }
}

.products__item {
  background-color: $color-background;
  position: relative;

  @include vp-1023 {
    margin-bottom: 30px;
  }
}

.products__heading {
  position: absolute;
  top: 48px;
  left: 40px;
  z-index: 2;

  max-width: 170px;
  margin: 0;

  font-size: 20px;
  line-height: 30px;
  font-family: $ff-roboto-condensed;
  color: $color-white;

  @include vp-1023 {
    top: 38px;
    left: 45px;

    max-width: 148px;
  }

  @include vp-767 {
    top: 25px;
    left: 30px;

    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    max-width: 130px;
  }
}

.products__img {
  position: relative;
  z-index: 1;

  img {
    width: 100%;

    @include vp-1023 {
      height: 250px;
    }
  }

  &::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 2;

    width: 60px;
    height: 60px;

    background-color: $color-deep-sky-blue;
    border-radius: 3px 0 0 0;

    @include vp-767 {
      width: 40px;
      height: 40px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    right: 26px;
    bottom: 25px;
    z-index: 3;

    width: 6px;
    height: 10px;

    background-image: url("../img/svg/array.svg");
    background-repeat: no-repeat;

    @include vp-767 {
      right: 16px;
      bottom: 15px;
    }
  }
}
